import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import md5 from 'js-md5';
import { of } from 'rxjs';
import { environment } from 'src/environments/environment';
import { map, switchMap } from 'rxjs/operators';

@Injectable()
export class AuthService {

  isLoggedIn = false;
  url = environment.url;

  user = {
    userName: null,
    amountAvailable: null,
    token: null,
    blockHeight: null,
    depositAccount: null,
    memo: null
  }

  constructor(
    private httpClient: HttpClient,
  ) { }

   login(loginObj) {
    this.isLoggedIn = false;
    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };
    let request = new URLSearchParams();
    request.set('username', loginObj.userName);

    if(loginObj.userName.toLowerCase() === 'sylvester') {
      request.set('password', loginObj.password);
    } else {
      request.set('password', md5(loginObj.password));
    }
    
    
    return this.httpClient.post( `${this.url}/getAccessToken`, request.toString(), options)
      .pipe(map((results: any) => {
        if(results.code === 200) {
          this.user.userName = loginObj.userName;
          if(results && results.msg) {
            this.user.amountAvailable = parseInt(results.msg.balance, 10) || 0;
            this.user.token = results.msg.token;
            this.user.memo = results.msg.memo;
            this.user.depositAccount = results.msg.depositaccount;
          }
          this.isLoggedIn = true;
          return this.user;
        }
      }))
  }

  register(registrationObj) {
    this.isLoggedIn = false;
    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };
    let request = new URLSearchParams();
    request.set('username', registrationObj.userName);
    request.set('password', md5(registrationObj.password));
    return this.httpClient.post( `${this.url}/register`, request.toString(), options)
      .pipe(switchMap((results: any) => {
        if(results.code === 200) {
          return this.login(registrationObj)
        }
        return of(null);
      }))
  }
}
